.hero {
    width: 100%;
    height: 100vh;
    position: relative;
}

.hero-mid {
    width: 100%;
    /*  about */
    height: 80vh;
    position: relative;
}

.hero-mid h1 {
    padding-top: 4rem !important;
    /*  about */
}


img {
    width: 100%;
    height: 100%;
    object-fit: cover;

}

.hero-text {
    position: absolute;
    top: 55%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
}

.hero-text h1 {
    font-size: 5rem;
    /* font size */
    font-weight: 800;
    background: #fff;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.hero-text p {
    font-size: 1.3rem;
    /* font size */
    color: #fff;

    /* padding: 0.5rem 0.2rem 0; */
}

.hero-text .show {
    text-decoration: none;
    background-color: #fff;
    padding: 1rem 2 rem;
    border-radius: 6px;
    font-size: 1.1rem;
    /*   button size issue */
    font-weight: bold;
    letter-spacing: 2px;
    color: #222;

}

@media screen and (max-width: 555px) {
    .hero-text h1 {
        padding: 10px 20px;
    }

    .hero-text p {
        font-size: 2rem;
        padding: 0 0 2rem 0;
    }

    .hero-text .show {
        padding: 0.6rem 1.1 rem;
        border-radius: 6px;
        font-size: 1rem;
        letter-spacing: 2px;
        background: red;
    }


}